import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { HfButtonComponent, HfCardComponent, HfInputComponent, HfRadioComponent, HfPipeLocationComponent, inputMsgType } from '@monorepo/ui';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { AppService, StoreService } from '@monorepo/utils';
import { CookieHelper } from '../../shared/services/cookie.helper';
import { Router } from '@angular/router';
import { URLS } from '../../shared/utils/urls';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-delivery-details',
  standalone: true,
  templateUrl: './delivery-details.component.html',
  styleUrl: './delivery-details.component.scss',
  imports: [HfCardComponent, HfInputComponent, HfRadioComponent, HfButtonComponent, ReactiveFormsModule, HfPipeLocationComponent]
})

export class DeliveryDetailsComponent implements OnInit, OnDestroy {

  orderID: number;
  orderParams: any;
  getDeliveryDetailsSubscription: Subscription;
  isSubmitButtonDisabled: boolean = false;
  submitButtonText: string = 'SUBMIT';

  deliveryDetailsForm = new FormGroup({
    houseDescription: new FormControl(),
    tankLocation: new FormControl(),
    fillLocationDescription: new FormControl(),
    tankPosition: new FormControl(),
    tankSize: new FormControl(),
  })

  get tankPosition() {
    return this.deliveryDetailsForm.get('tankPosition') as FormControl
  }

  get tankLocation() {
    return this.deliveryDetailsForm.get('tankLocation') as FormControl
  }

  get houseDescription() {
    return this.deliveryDetailsForm.get('houseDescription') as FormControl
  }

  get fillLocationDescription() {
    return this.deliveryDetailsForm.get('fillLocationDescription') as FormControl
  }

  get tankSize() {
    return this.deliveryDetailsForm.get('tankSize') as FormControl
  }

  tankSizeIM: string = 'Invalid Tank Size';
  tankSizeMT: inputMsgType = '';

  constructor(
    private http: HttpClient,
    private storeService: StoreService,
    private cookieHelper: CookieHelper,
    private router: Router,
    public appService: AppService,
  ) { }

  ngOnInit() {
    if (this.appService.isBrowserRendering) {
      this.storeService.getValueFromStore('orderParams', (orderParams) => {
        this.orderParams = JSON.parse(JSON.stringify(orderParams));
        this.orderID = this.orderParams['orderId'];
        this.getDeliveryDetails();
        this.storeService.deleteKeyFromStore('orderParams');
      });
    }
  }

  getDeliveryDetails() {
    let deliveryId = this.orderParams['addressId'];
    let companyId = this.orderParams['companyId'];
    const url = `${environment.api_url}customer/order/delivery-details?deliveryId=${deliveryId}&companyId=${companyId}`;
    this.getDeliveryDetailsSubscription = this.http.get(url).subscribe({
      next: (response) => {
        if (response['houseDescription']) {
          this.houseDescription.setValue(response['houseDescription']);
        }
        if (response['fillLocationDescription']) {
          this.fillLocationDescription.setValue(response['fillLocationDescription']);
        }
        this.tankLocation.setValue(response['tankLocation']);
        this.tankSize.setValue(response['tankSize'])
        this.tankPosition.setValue(response['tankPosition']);
      }
    });
  }

  validateDeliveryDetailsForm() {
    if (this.tankSize.value != '') {
      this.tankSize.setValidators([Validators.min(100)]);
    } else {
      this.tankSize.clearValidators();
    }
    this.tankSize.updateValueAndValidity();
    if (this.deliveryDetailsForm.invalid) {
      if (this.tankSize.invalid) {
        this.tankSize.markAsTouched({ onlySelf: true });
        this.tankSizeMT = 'error';
        if (this.tankSize.errors?.['min']) {
          this.tankSizeIM = 'Minimum Tank Size is 100 gallons';
        }
        let vaildtankSize = this.tankSize.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.tankSizeIM = '';
            this.tankSizeMT = '';
            vaildtankSize.unsubscribe();
          }
        });
      }
      return;
    }
    this.deliveryDetailsFormSubmit();
  }

  deliveryDetailsFormSubmit() {
    this.submitButtonText = 'SUBMITTING...';
    this.isSubmitButtonDisabled = true;
    const url = `${environment.api_url}customer/order/delivery-details`;
    const body = {
      Id: this.orderParams['addressId'],
      userId: this.cookieHelper.getUserID(),
      fillLocationDescription: this.fillLocationDescription.value,
      houseDescription: this.houseDescription.value,
      tankPosition: this.tankPosition.value,
      tankLocation: this.tankLocation.value,
      tankSize: this.tankSize.value,
      propertyArea: 0,
      termostatTemperature: 0,
    };

    return this.http.put(url, body).subscribe({
      next: () => {
        this.submitButtonText = 'SUBMIT';
        this.isSubmitButtonDisabled = false;
        this.router.navigate([URLS.ORDERS]);
      },
      error: (err) => {
        console.log(err);
        this.submitButtonText = 'SUBMIT';
        this.isSubmitButtonDisabled = false;
      }
    })
  }

  ngOnDestroy() {
    if (this.getDeliveryDetailsSubscription) {
      this.getDeliveryDetailsSubscription.unsubscribe();
    }
  }
}
